import { Routes, Settings, VarTypes } from '@/utils/enums';
import SettingsMixin from '@/mixins/settings';
import Head from '@/views/Head.vue';
import { mapActions, mapGetters } from 'vuex';
export default {
  name: 'Sudo',
  components: {
    // eslint-disable-next-line vue/no-reserved-component-names
    Head
  },
  mixins: [SettingsMixin],
  data() {
    return {
      Routes,
      form: {
        password: '',
        hidden: ''
      },
      rules: {
        password: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }]
      },
      passwordType: 'password',
      showContent: false,
      inProgress: false,
      showUsersPasswords: false,
      changeNewAccountsPasswords: false
    };
  },
  computed: {
    ...mapGetters({
      getSettingValue: 'setting/getSettingValue'
    })
  },
  watch: {
    showUsersPasswords: {
      handler(val, oldVal) {
        this.updateEnable(Settings.SHOW_USERS_PASSWORDS.NAME, val);
      }
    },
    changeNewAccountsPasswords: {
      handler(val, oldVal) {
        this.updateEnable(Settings.CHANGE_NEW_ACCOUNTS_PASSWORDS.NAME, val);
      }
    }
  },
  created() {
    if (!this.userData || parseInt(this.userData.access) > 0) {
      this.$router.replace(Routes.LOGIN.PATH);
    }
    this.showUsersPasswords = this.getSettingValue(Settings.SHOW_USERS_PASSWORDS.NAME, VarTypes.BOOLEAN);
    this.changeNewAccountsPasswords = this.getSettingValue(Settings.CHANGE_NEW_ACCOUNTS_PASSWORDS.NAME, VarTypes.BOOLEAN);
  },
  methods: {
    ...mapActions('setting', ['getAllSettings', 'updateSetting']),
    submit() {
      this.$refs.form.validate(async valid => {
        if (valid) {
          this.inProgress = true;
          this.showContent = this.form.password == this.suPassword;
          if (!this.showContent) this.$message.error(this.$t('wrongPassword'));
          this.inProgress = false;
        } else {
          return false;
        }
      });
    },
    showPwd() {
      this.passwordType = this.passwordType === 'password' ? '' : 'password';
      this.$nextTick(() => {
        this.$refs.password.focus();
      });
    },
    async updateEnable(name, value) {
      await this.updateSetting({
        name: name,
        value: value ? 1 : 0
      });
      await this.getAllSettings();
    }
  }
};