import { genPassword } from '@/utils/functions';
import MailingMixin from '@/mixins/mailing';
import { USER_CONTEXT } from '@/store/modules/database/user';
import { mapActions } from 'vuex';
export default {
  name: 'ResetPassword',
  mixins: [MailingMixin],
  data() {
    return {
      users: [],
      isLoading: false,
      showTooltip: false,
      isProceeding: false,
      form: {
        idOrEmail: ''
      },
      rules: {
        idOrEmail: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }]
      },
      appName: process.env.VUE_APP_NAME
    };
  },
  created() {
    this.init();
  },
  methods: {
    ...mapActions('user', ['getAllUsers']),
    async init() {
      this.isLoading = true;
      const resp = await this.getAllUsers();
      if (resp.success && resp.data) this.users = resp.data;
      this.isLoading = false;
    },
    submitForm() {
      this.$refs.form.validate(async valid => {
        if (valid) {
          this.isProceeding = true;
          if (this.$validator.isEmail(this.form.idOrEmail)) {
            const user = this.users.find(u => u.email == this.form.idOrEmail);
            if (user) await this.proceed(user);else this.$message.error(this.$t('emailNotAssociatedWithAnyAccount'));
          } else {
            const user = this.users.find(u => u.uid == this.form.idOrEmail);
            if (user) {
              if (user.email) await this.proceed(user);else this.$message.error(this.$t('noEmailDefinedForThisAccount'));
            } else this.$message.error(this.$t('idNotAssociatedWithAnyAccount'));
          }
          this.isProceeding = false;
        } else return false;
      });
    },
    async proceed(user) {
      const password = genPassword(13, {
        symbols: true
      });
      const sendTask = await this.sendEmail(null, `${this.$t('passwordReset')} - ${this.appName}`, `${this.$t('passwordReset')} - ${this.appName}`, `<small>${this.$t('passwordSuccessfullyReset')}. <br> ${this.$t('urNewPasswordIs')} : <b>${password}</b></small>`, user.email);
      if (sendTask) {
        const updateTask = await this.updateItemInDB({
          context: USER_CONTEXT,
          id: user.id,
          data: {
            password,
            last_login: null
          }
        });
        if (updateTask.success) {
          this.$message.success(this.$t('passwordResetSuccess'));
          this.form.idOrEmail = '';
        } else this.$message.error(this.$t('resetFailed'));
      } else this.$message.error(this.$t('emailAddressNotFound'));
    }
  }
};