import { formatDate } from '@/utils/chronos';
import { Routes } from '@/utils/enums';
import { USER_CONTEXT } from '@/store/modules/database/user';
import Head from '@/views/Head.vue';
export default {
  name: 'ChangePassword',
  components: {
    // eslint-disable-next-line vue/no-reserved-component-names
    Head
  },
  data() {
    return {
      form: {
        password: '',
        confirmPassword: ''
      },
      rules: {
        password: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }],
        confirmPassword: [{
          required: true,
          message: this.$t('fieldRequired'),
          trigger: 'blur'
        }]
      },
      passwordType: 'password',
      inProgress: false
    };
  },
  methods: {
    submit() {
      this.$refs.form.validate(async valid => {
        if (valid) {
          if (this.form.password.length < 6) {
            this.$message.warning(this.$t('passwordTooShort'));
            return false;
          }
          if (this.form.password !== this.form.confirmPassword) {
            this.$message.warning(this.$t('passwordsDoesNotMatch'));
            return false;
          }
          this.inProgress = true;
          const task = await this.updateItemInDB({
            context: USER_CONTEXT,
            id: this.userData.id,
            data: {
              password: this.form.password
            }
          });
          if (task.success) {
            this.$message.success(this.$t('success'));
            await this.updateItemInDB({
              context: USER_CONTEXT,
              id: this.userData.id,
              data: {
                last_login: formatDate(new Date(), {
                  outPattern: 'YYYY-MM-DD hh:mm:ss',
                  locale: this.$i18n.locale
                })
              }
            });
            setTimeout(() => {
              this.$router.replace(Routes.HOME.PATH);
            }, 1000);
          } else {
            this.$message.error(this.$t('anErrorOccured'));
          }
          this.inProgress = false;
        } else {
          return false;
        }
      });
    },
    showPwd() {
      this.passwordType = this.passwordType === 'password' ? '' : 'password';
      this.$nextTick(() => {
        this.$refs.password.focus();
      });
    }
  }
};