import WhiteLogo from '@/assets/images/breeze-logo-white.svg';
import Logo from '@/assets/images/breeze-logo.svg';
import { formatDate } from '@/utils/chronos';
import { API_REQUEST_HEADERS, APP_BASE_URL } from '@/utils/constants';
import { AccountStatus, Routes, VarTypes } from '@/utils/enums';
import SettingsMixin from '@/mixins/settings';
import { SETTING_CONTEXT } from '@/store/modules/database/setting';
import { USER_CONTEXT } from '@/store/modules/database/user';
import Head from '@/views/Head.vue';
import ResetPassword from '@/views/Login/ResetPassword';
import { mapActions, mapGetters, mapMutations } from 'vuex';
export default {
  name: 'Login',
  components: {
    ResetPassword,
    // eslint-disable-next-line vue/no-reserved-component-names
    Head
  },
  mixins: [SettingsMixin],
  data() {
    const validateUsername = (rule, value, callback) => {
      if (value) callback();else callback(new Error(this.$t('idRequired')));
    };
    const validatePassword = (rule, value, callback) => {
      if (value) callback();else callback(new Error(this.$t('passwordRequired')));
    };
    return {
      loginForm: {
        username: '',
        password: ''
      },
      loginRules: {
        username: [{
          required: true,
          trigger: 'blur',
          validator: validateUsername
        }],
        password: [{
          required: true,
          trigger: 'blur',
          validator: validatePassword
        }]
      },
      sessionExpiredMessage: null,
      checkingCredentials: false,
      resetPasswordDialog: false,
      checkingAppConfig: false,
      serverIsReachable: false,
      rememberMe: false,
      WhiteLogo,
      Logo,
      devModeBaseUrl: '',
      database: {
        username: '',
        password: '',
        prefix: '',
        dbname: '',
        host: ''
      }
    };
  },
  computed: {
    ...mapGetters({
      isUserConnected: 'session/isUserConnected'
    }),
    dividerBgColor() {
      return this.pickByTheme('white', '#282828');
    },
    dividerTextColor() {
      return this.pickByTheme('#303133', 'whitesmoke');
    }
  },
  created() {
    if (this.$isMobile()) this.$router.replace(Routes.NO_MOBILE_SUPPORT.PATH);
    this.checkEverything();
  },
  methods: {
    ...mapMutations('session', ['updateUserData', 'updateConnected', 'updateRememberMe', 'updateSessionExpired', 'updateDevModeBaseUrl']),
    ...mapMutations('global', ['updateRedirectToPanelPath']),
    ...mapActions({
      getUserByCredentials: 'authentication/getUserByCredentials',
      getAllAdministrators: 'user/getAllAdministrators',
      fetchDbConfig: 'setting/fetchDbConfig'
    }),
    async checkEverything() {
      if (this.isUserConnected) {
        if (this.$store.state.global.previousRouteName) this.updateRedirectToPanelPath(false);else this.updateRedirectToPanelPath(true);
        this.$router.replace(Routes.HOME.PATH);
      }
      this.devModeBaseUrl = this.devMode ? this.$store.state.session.devModeBaseUrl || APP_BASE_URL : APP_BASE_URL;
      this.checkingAppConfig = true;
      const loading = this.$loading({
        lock: true,
        text: this.$t('loading'),
        background: 'rgba(0, 0, 0, 0.8)'
      });
      this.axios.request({
        method: 'get',
        baseURL: this.devModeBaseUrl + '/breeze-server/db-api/',
        headers: API_REQUEST_HEADERS,
        url: `/${SETTING_CONTEXT}/check/server`
      }).then(resp1 => {
        this.axios.request({
          method: 'get',
          baseURL: this.devModeBaseUrl + '/breeze-server/db-api/',
          headers: API_REQUEST_HEADERS,
          data: this.database,
          url: `/${SETTING_CONTEXT}/check/database`
        }).then(async resp2 => {
          if (resp2.data === true) {
            const task = await this.getAllAdministrators();
            let nb = 0;
            if (task.success) nb = (task.data || []).length;
            loading.close();
            if (nb) {
              this.checkingAppConfig = false;
              this.serverIsReachable = true;
              if (this.$store.state.session.sessionExpired) {
                this.sessionExpiredMessage = this.$message({
                  message: this.$t('sessionExpired'),
                  showClose: true,
                  type: 'error',
                  duration: 0
                });
                this.updateSessionExpired(false);
              }
            } else {
              this.$router.replace({
                name: Routes.INSTALL.NAME,
                params: {
                  step: 1
                }
              });
            }
          } else {
            loading.close();
            this.$router.replace({
              name: Routes.INSTALL.NAME
            });
          }
        }).catch(err2 => {
          loading.close();
          this.$router.replace({
            name: Routes.INSTALL.NAME
          });
        });
      }).catch(err1 => {
        this.checkingAppConfig = false;
        this.serverIsReachable = false;
        loading.close();
      });
      const dbConfig = await this.fetchDbConfig();
      if (dbConfig.success && dbConfig.data && typeof dbConfig.data === VarTypes.OBJECT) this.database = dbConfig.data;
    },
    submitConfigData() {
      this.axios.request({
        method: 'get',
        baseURL: this.devModeBaseUrl + '/breeze-server/db-api/',
        headers: API_REQUEST_HEADERS,
        url: `/${SETTING_CONTEXT}/check/server`
      }).then(resp1 => {
        this.updateDevModeBaseUrl(this.devModeBaseUrl);
        location.reload();
      }).catch(err1 => {
        this.$message.error(this.$t('unableToConnectToServerOn', [this.devModeBaseUrl]));
      });
    },
    async login() {
      this.$refs.loginForm.validate(async valid => {
        if (valid) {
          this.checkingCredentials = true;
          const resp = await this.getUserByCredentials({
            username: this.loginForm.username?.trim(),
            password: this.loginForm.password
          });
          if (resp.success) {
            if (resp.data.username && resp.data.password && resp.data.user) {
              if (resp.data.user.status == AccountStatus.ENABLED) {
                this.updateConnected(true);
                this.updateSessionExpired(false);
                this.updateUserData(resp.data.user);
                this.updateRememberMe(this.rememberMe);
                if (this.sessionExpiredMessage) this.sessionExpiredMessage.close();
                if (this.changeNewAccountsPasswords) {
                  if (resp.data.user.last_login) await this.openHome(resp.data.user);else this.$router.replace(Routes.CHANGE_PASSWORD.PATH);
                } else {
                  await this.openHome(resp.data.user);
                }
              } else {
                this.$message.warning(this.$t('disabledAccount'));
              }
            } else if (resp.data.username) {
              this.$message.error(this.$t('wrongPassword'));
            } else {
              this.$message.error(this.$t('userDontExist'));
            }
          } else {
            this.$message.error(this.$t('anErrorOccured'));
          }
          this.checkingCredentials = false;
        } else {
          return false;
        }
      });
    },
    async openHome(user) {
      await this.updateItemInDB({
        context: USER_CONTEXT,
        id: user.id,
        data: {
          last_login: formatDate(new Date(), {
            outPattern: 'YYYY-MM-DD hh:mm:ss',
            locale: this.$i18n.locale
          })
        }
      });
      this.updateRedirectToPanelPath(true);
      this.$router.replace(Routes.HOME.PATH);
    }
  }
};